<template>
    <div>
        <div class="container">
            <el-table :data="tableData" border v-loading="loading">
                <el-table-column prop="role_name" label="工作流权限" width="120">
                </el-table-column>
                <el-table-column label="岗位">
                    <template slot-scope="{row, $index}">
                        <el-tag v-for="item in row.position">{{item.pos_name}}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="操作" width="180" align="center" fixed="right">
                    <template slot-scope="{row, $index}">
                        <el-button type="text" icon="el-icon-edit" @click="handleEdit(row, $index)">编辑</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!-- <div class="pagination">
                <el-pagination background @current-change="handleCurrentChange" layout="prev, pager, next" :total="1000">
                </el-pagination>
            </div> -->
        </div>
        
        <!-- 编辑拥有该权限的岗位 -->
        <el-dialog title="编辑拥有该权限的岗位" :visible.sync="dialogPost" width="50%">
            <el-form ref="formPost" :model="formPost" label-width="140px" label-position="right">
                <el-form-item label="工作流权限">
                    {{formPost.role_name}}
                </el-form-item>
                <el-form-item label="拥有该权限的岗位">
                    <el-select v-model="formPost.pos_id" multiple placeholder="请选择">
                        <el-option
                            v-for="item in optionsPost"
                            :key="item.id"
                            :label="item.position_name"
                            :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogPost = false">取 消</el-button>
                <el-button type="primary" @click="dialogSubmitPost">确 定</el-button>
            </span>
        </el-dialog>
    
    </div>
</template>

<script>
    import * as API from '@/api/enterpriseManagement'
    export default {
        name: 'basetable',
        data() {
            return {
                tableData: [
                ],
                page: 1,
                loading: false,
                dialogPost: false,
                formPost: {
                    role_name:'',
                    pos_id:[]
                },
                optionsPost: []
            }
        },
        mounted() {
            this.getData()
            this.getPostList()
        },
        computed: {

        },
        methods: {
            // 分页导航
            handleCurrentChange(val) {
                this.page = val;
                this.getData();
            },
            getData() {
                this.loading = true
                API.AUAUDIbntList({}).then(res => {
                    this.loading = false
                    this.tableData = res.data;
                })
            },
            getPostList() {
                API.postManagementList({}).then(res => {
                    this.optionsPost = res.data;
                })
            },
            handleEdit(row, index) {
                this.dialogPost = true;
                Object.assign(this.formPost,row)
            },
            dialogSubmitPost(){
                API.AUAUDIbntEdit(this.formPost).then(res => {
                    this.dialogPost = false
                    this.getData();
                })
            }
        }
    }

</script>

<style scoped>
    .handle-box {
        margin-bottom: 20px;
    }

    .handle-select {
        width: 120px;
    }

    .handle-input {
        width: 300px;
        display: inline-block;
    }
    .del-dialog-cnt{
        font-size: 16px;
        text-align: center
    }
    .table{
        width: 100%;
        font-size: 14px;
    }
    .red{
        color: #ff0000;
    }
</style>
